main {
  display: block;
  position: relative;
  z-index: 2;
  width: 100%;
  background-color: #131315;
  /* max-width: 1280px; */
  overflow: hidden;
}

.main-content {
  background-color: #0000003b;
  margin: 0 auto;
  box-shadow: 0px 0px 4px #ddd;
  /* padding: 2rem; */
  width: 100%;
  /* max-width: 1100px; */
  max-width: 2560px;
  border-radius: 5px;
}
.Oval {
  position: fixed;
  width: 96px;
  height: 96px;
  background-color: #e34141;
  z-index: 4;
  border-radius: 50%;
  right: 17px;
  bottom: 23px;
  cursor: pointer;
  font-family: Nafta;
  font-size: 22.5px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.Oval:hover {background-color: #943232}
.Oval:active {
  background-color: #803636;
    box-shadow: 0 5px #6666;
    transform: translateY(4px);
}
@media only screen and (min-width: 600px) {
  .main-content {
    align-items: flex-start;
  }
}
@media only screen and (max-width: 600px) {
  .Oval {
    bottom: 77px;
  }
}
