footer{
  background-image: linear-gradient(to bottom, #31180f, #000);
  height: auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 2rem 0 1rem;
}
.logo-footer{
  display: none;
}
.coypright{
  font-family: SFProDisplay;
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #fff;
}


@media (max-width: 600px) {
  footer{
    padding: 2rem 0 3rem;
  }
  .logo-footer{
    display: block;
    width: 100%;
    max-width: 284px;
    margin: auto;
    padding: 4rem 0;
  }
}
